import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
* Gets an array of quizzes
* @returns {Promise} response - the response of quizzes
*/
export function getQuizzes(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/quiz?limit=${limit}&offset=${offset}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches a single quiz object
* @param {String} id - quiz Id
* @returns {Promise} response - a promise of a single quiz
*/
export function getQuiz(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/quiz/${id}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Creates a quiz
* @param {Object} quizData - quiz object
* @returns {Promise} response - a promise of a single quiz to be created
*/
export function createQuiz(quizData) {
	if (isLoggedIn()) {
		return axios.post('/v1/quiz', quizData);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Creates a quiz question
* @param {Object} data - quiz question object
* @param {String} id - quiz id
* @returns {Promise} response - a promise of a single quiz question to be created
*/
export function createQuestions(id, data) {
	if (isLoggedIn()) {
		return axios.post(`/v1/quiz/${id}/questions`, data);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Deletes a quiz question
* @param {String} id - quiz id
* @param {Object} questionId - quiz question Id
* @returns {Promise} response - a promise of a single quiz question to be deleted
*/
export function deleteQuestion(id, questionId) {
	if (isLoggedIn()) {
		return axios.delete(`/v1/quiz/${id}/questions/${questionId}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches all quiz questions
* @param {String} id - Quiz Id
* @returns {Promise} response - a promise of questions to be fetched
*/
export function getQuestions(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/quiz/${id}/questions`);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches a quiz question
* @param {String} id - Quiz Id
* @param {String} questionId - Question Id
* @returns {Promise} response - a promise of questions to be fetched
*/
export function getQuestion(id, questionId) {
	if (isLoggedIn()) {
		return axios.get(`/v1/quiz/${id}/questions/${questionId}`);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Updates a question
* @param {String} id - Quiz Id
* @param {String} questionId - Question Id
* @returns {Promise} response - a promise of questions to be fetched
*/
export function updateQuestion(data, questionData) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/quiz/${data.id}/questions/${data.questionId}`, questionData);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

