<template>
	<div>
		<template v-if="loadingQuestion">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<section class="main-grid__row">
				
				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="activity-tab" data-toggle="tab" href="#activity" role="tab" aria-controls="activity" aria-selected="true">Edit Test</a>
						</li>
					</ul>
					<div class="tab-pane fade show active user-profile__tabs__section" id="settings" role="tabpanel" aria-labelledby="settings-tab">
						<div class="settings">
							<form
								method="post"
								@submit.prevent="updateQuestion">
								<div class="settings__field">
									<div class="form-group">
										<label class="modal__label" for="option-a">Option A</label>
										<quill-editor ref="myTextEditor"
											v-model="itemData.option_a"
											:options="editorOption">
										</quill-editor>
										<div class="invalid-feedback">
											{{ errors.option_a }}
										</div>
									</div>

									<div class="form-group">
										<label class="modal__label" for="option-b">Option B</label>
										<quill-editor ref="myTextEditor"
											v-model="itemData.option_b"
											:options="editorOption">
										</quill-editor>
										<div class="invalid-feedback">
											{{ errors.option_b }}
										</div>
									</div>

								</div>
								<div class="settings__primary">
									<button
										class="btn btn__save"
										type="button"
										name="button"
										@click.prevent="updateQuestion"
										v-bind="{'disabled': loading}">
										Save
									</button>
									<Loader v-if="updating"/>
								</div>
							</form>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import { updateQuestion, getQuestion } from '../../utils/api/quiz';
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import questions from '../../utils/validations/questions';
import { success, fail } from '../../utils/toast';

export default {
	name: 'QuestionDetail',
	mixins: [form],
	data() {
		return {
			loadingQuestion: true,
			itemData: {},
			errors: {},
			updating: false,
			questionsLoaded: false,
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	components: {
		Loader,
		quillEditor
	},
	created() {
		this.getQuestionData();
	},
	methods: {
		getQuestionData() {
			getQuestion(this.$route.params.id, this.$route.params.questionId)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.loadingQuestion = false;
						this.questionsLoaded = true;
						return true;
					}
					this.questionsLoaded = false;
					return false;
				})
				.catch((error) => {
					this.loadingQuestion = false;
					this.questionsLoaded = false;
					return error;
				});
			return true;
		},
		updateQuestion() {
			const result = { option_a: this.itemData.option_a, option_b: this.itemData.option_b };

			this.validate(result, questions.create);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.updating = true;
			const data = {
				id: this.$route.params.id,
				questionId: this.$route.params.questionId
			};
			updateQuestion(data, result)
				.then((response) => {
					if (response.status === 200) {
						this.updating = false;
						success({
							title: 'Success! Test has been updated'
						});
					}
				})
				.catch((error) => {
					this.updating = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	computed: {
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>

